<template>
  <v-container>
    <v-card>
      <v-card-title
        ><h2 class="mx-auto text-center">Problem Solving</h2></v-card-title
      >
      <v-card-text>
        <v-row justify="center" class="my-3">
          <v-btn @click="filterStatus('')" class="success">All</v-btn>
          <v-btn @click="filterStatus('solved')" class="success mx-3"
            >Solved</v-btn
          >
          <v-btn @click="filterStatus('unsolved')" class="success"
            >Unsolved</v-btn
          >
        </v-row>
        <v-list>
          <v-list-item v-for="(p, i) in problems" :key="i">
            <v-list-item-content class="problem_solving">
              <div>
                <p>{{ p.name }}-{{ p.roll }}</p>
                <p>Problem:</p>
                <p>{{ p.problem }}</p>
              </div>

              <div class="my-2">
                <p>Solution</p>
                <p v-if="p.solution">{{ p.solution }}</p>
                <v-btn @click="openForEdit(p)" class="indigo text-center" dark
                  >give solution</v-btn
                >
                <v-btn class="mx-2" outlined v-if="p.rate"
                  >success rate: {{ p.rate }}</v-btn
                >
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
    <v-dialog v-model="op" max-width="600">
      <v-card>
        <v-card-title>
          <h3 class="text-center mx-auto mb-2">Submit your problem</h3>
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid" lazy-validation ref="form">
            <v-textarea
              outlined
              label="Write about your solution"
              v-model="problem.solution"
            ></v-textarea>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="mx-auto success"
            large
            tile
            @click="askQuestion"
            :loading="loading"
            >submit <v-icon>mdi-send</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: true,
      op: false,
      loading: false,
      problems: [],
      problem: {},
      students: [],
      allproblems: [],
    };
  },
  methods: {
    openForEdit(p) {
      this.problem = p;
      this.op = true;
    },
    getQuestions() {
      fetch(
        "https://script.google.com/macros/s/AKfycbyZnrdnsMUrnscQLqc0Os5A1tq8F2uxbILcIv4Y59mN7YnfKaEIIreiaIG1DfHGaDep/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.problems = data.routines
            .map((r) => ({
              ...r,
              name: this.students.find((s) => s.roll == r.roll).Name,
            }))
            .sort((a, b) => b.id - a.id);
          this.allproblems = this.problems;
        });
    },
    filterStatus(s) {
      if (s == "solved") {
        this.problems = this.allproblems.filter((s) => s.solution != "");
      } else if (s == "unsolved") {
        this.problems = this.allproblems.filter((s) => s.solution == "");
      } else {
        this.problems = this.allproblems;
      }
    },
    askQuestion() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        fetch(
          "https://script.google.com/macros/s/AKfycbyZnrdnsMUrnscQLqc0Os5A1tq8F2uxbILcIv4Y59mN7YnfKaEIIreiaIG1DfHGaDep/exec",
          {
            method: "POST",
            mode: "no-cors",
            cache: "no-cache",
            redirect: "follow",
            body: JSON.stringify({
              type: "edit",
              ...this.problem,
            }),
          }
        ).then(() => {
          this.getQuestions();
          this.op = false;
          this.loading = false;
        });
      }
    },
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxe4ivZ8K8s9VjqQO8-XdENxSFRHZQusf6xzb5IECfH7j-oZj6aFpX5XRjnMt0zCPAP8w/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.students = data.students;
          this.getQuestions();
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.problem_solving {
  font-size: 16px;
  padding: 10px;
  margin-bottom: 15px;

  border-radius: 5px;
  background: linear-gradient(145deg, #c2c5d6, #e7eaff);
  box-shadow: 2px 2px 6px #b8baca, -2px -2px 6px #f8fcff;

  div:first-child {
    background: rgb(221, 224, 217);
    border-radius: 4px;
    padding: 10px;
  }
  div:nth-child(2) {
    background: rgb(133, 243, 142);

    border-radius: 4px;
    padding: 10px;
  }
  div:last-child {
    margin-top: 10px;
    border-radius: 4px;
    padding-top: 15px;
    background: rgb(241, 238, 238);
  }
}
</style>